.card{
    width: 220px;
    height: 320px;
    position: relative;
    perspective: 1000px; /*Добавляем глубину, чтобы при повороти карточки был эффект 3D*/
	cursor: pointer;
	-webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.card:active {
	-webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.front, .back{
    position: absolute;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items:center;
    transition: 1s all ease;
    backface-visibility: hidden; /*скрываем тыльную часть карточки, это нужно что бы не видеть тыльную часть после поворота на 180 градусов*/
    border-radius: 20px;
}

.frontActive,.backActive {
	position: absolute;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items:center;
    transition: 1s all ease;
    backface-visibility: hidden; /*скрываем тыльную часть карточки, это нужно что бы не видеть тыльную часть после поворота на 180 градусов*/
    border-radius: 20px;
} 

.front {
	-webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
	background: '#f5f5f5';
}

.back{
    background-color: #BDBDBD;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.frontActive{
	background: '#f5f5f5';
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}
.backActive {
	background-color: #BDBDBD;
	-webkit-transform: rotateY(360deg);
	        transform: rotateY(360deg);
}
